angular.module('app').component('detailPanel', {
  transclude: true,
  template: require('./detailPanel.html'),
  bindings: {
    title: '@',
    initialCollapsed: '@collapsed'
  },
  controller: function() {
    this.collapsed = (this.initialCollapsed === 'true');

    this.collapse = function() {
      this.collapsed = !this.collapsed;
    }
  }
})










